export class Ajax extends Component {
  state = {
    data: [],
    pagination: {},
    loading: false
  };

  componentDidMount() {
    this.fetch();
  }

  handleTableChange = (pagination, filters, sorter) => {
    const pager = { ...this.state.pagination };
    pager.current = pagination.current;
    this.setState({
      pagination: pager
    });
    this.fetch({
      results: pagination.pageSize,
      page: pagination.current,
      sortField: sorter.field,
      sortOrder: sorter.order,
      ...filters
    });
  };

  fetch = (params = {}) => {
    var devicesRef = db.collection("/tenants/EB@zebra.com/devices");
    console.log("params:", params);
    this.setState({ loading: true });
    devicesRef.get().then(snapshot => {
      console.log("done loading");
      console.log(snapshot);
      var devices = [];
      snapshot.forEach(doc => {

        console.log('inside getDevicePoolDoc doc.id.. ', doc.id, ' data ', doc.data())
  
        if (doc.exists) {
  
          devices.push(doc.data())
  
        }
  
      })      
      const pagination = { ...this.state.pagination };
      // Read total count from server
      // pagination.total = data.totalCount;
      pagination.total = 14;
      this.setState({
        loading: false,
        data: devices,
        pagination
      });
    });
  };

  render() {
    return (
        <Card title="Enrolled Devices " >
            <Table 
              className="no-border-last" 
              columns={columns} 
              dataSource={this.state.data}
        pagination={this.state.pagination}
        loading={this.state.loading}
        onChange={this.handleTableChange}
            />
          </Card>

      
    );
  }
}

export default Ajax;
